const w = {
    s : { // Single
        0       : { p : 0.1,  s : 0 },
        9700    : { p : 0.12, s : 970 },
        39475   : { p : 0.22, s : 4543 },
        84200   : { p : 0.24, s : 14382 },
        160725  : { p : 0.32, s : 32748 },
        204100  : { p : 0.35, s : 46628 },
        510300  : { p : 0.37, s : 153798 },
        1000000 : { p : 0.37, s : 334987 }
    },
    m : { // Married
        0       : { p : 0.1,  s : 0 },
        19400   : { p : 0.12, s : 1940 },
        78950   : { p : 0.22, s : 9086 },
        168400  : { p : 0.24, s : 28765 },
        321450  : { p : 0.32, s : 65497 },
        408200  : { p : 0.35, s : 93257 },
        612350  : { p : 0.37, s : 164709 },
        1000000 : { p : 0.37, s : 226569 }
    }
}

const dedcution = {
    s : 12200,
    m: 24400
}

export const calculateFed = (g, status) => {
    g -= dedcution[status];

    var taxableAmount = g;

    if (g < 0) {
        return 0;
    }

    var b = Object.keys(w[status]);
    console.log(b);

    for (var i = 0; i < b.length; i++) { // Find bracket
        if (b[i] > g || b[i] === 1000000)  {
            g -= b[i-1]; // Get taxable income
            b = w[status][b[i-1]]; // Set bracket
            console.log(b);
            console.log('Taxable income: ' + g);
            var tax = ((b.p*(g)) + b.s);
            return {tax: Math.round(tax), taxableAmount: taxableAmount};
        }    
    }

    return {};
 }