import React from 'react';

import { Container, CssBaseline, Avatar, Typography, TextField, Button, Grid, Table, TableHead, TableRow, TableBody, TableCell } from '@material-ui/core';
import { calculateFed } from '../util/calcutil'

class CalculatorComponent extends React.Component {
    constructor(props) {
        super(props);

        this.handlechange = this.handlechange.bind(this);

        var res = calculateFed(100000, 'm');
        this.state = {income: 100000, tax: res.tax, taxableAmount: res.taxableAmount};
        
    }
      
    handlechange(event) {
        var res = calculateFed(event.target.value, 'm');
        this.setState({
            income: event.target.value,
            tax: res.tax,
            taxableAmount: res.taxableAmount
        });
    }

    render() {
      return (
        <form className={this.props.useClasses.root} noValidate autoComplete="off">
            <Grid container spacing={2}>
                <Grid item xs={12}>
                    <Typography variant="h5" align={'center'}>US Income Tax Calculator</Typography>    
                </Grid>
                <Grid item xs={12}>
                    <TextField style={{width: '100%'}} id="outlined-basic" label="Income amount $" variant="outlined" onChange={this.handlechange} value={this.state.income} />
                </Grid>
                <Grid item xs={12}>
                    <Table size="small">
                        <TableHead>
                            <TableRow>
                                <TableCell colSpan={2} style={{fontWeight : 600}}>Federal Taxes</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                        <TableRow>
                            <TableCell>Taxable Income</TableCell>
                                <TableCell align="right">{'$' + this.state.taxableAmount}</TableCell>
                            </TableRow>                            <TableRow>
                                <TableCell>Federal Tax</TableCell>
                                <TableCell align="right">{'$' + this.state.tax}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell colSpan={2} style={{fontWeight : 600}}>California State Taxes</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Taxable Income</TableCell>
                                <TableCell align="right">{'$' + this.state.taxableAmount}</TableCell>
                            </TableRow>                            <TableRow>
                                <TableCell>Federal Tax</TableCell>
                                <TableCell align="right">{'$' + this.state.tax}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell colSpan={2} style={{fontWeight : 600}}>Totals</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Total Tax</TableCell>
                                <TableCell align="right">{'$' + this.state.taxableAmount}</TableCell>
                            </TableRow>                            
                            <TableRow>
                                <TableCell>Monthly Take-Home</TableCell>
                                <TableCell align="right">{'$' + this.state.tax}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell style={{fontWeight : 600}}>Annual Take-Home</TableCell>
                                <TableCell style={{fontWeight : 600}} align="right">{'$' + this.state.tax}</TableCell>
                            </TableRow>                            
                        </TableBody>
                    </Table>
                </Grid>
            </Grid>
        </form>
      );
    }
  }

export default CalculatorComponent;